.autumn__btn {
  display: inline-block;
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  line-height: 1;
  -webkit-appearance: none;
  margin: 0;
  padding: 12px 20px;
  border-width: 1px;
  border-style: solid;
  border-color: #bfcbd9;
  -o-border-image: initial;
     border-image: initial;
  border-radius: 4px;
  color: #495060;
  background: white;
  font-size: 1.6rem;
  cursor: pointer;
  -wekit-border-radius: 4px;
  border-radius: 4px; }
.autumn__btn:hover, .autumn__btn:active {
    text-decoration: none;
    opacity: 0.8; }
.autumn__btn[disabled] {
    cursor: not-allowed;
    opacity: 0.5; }
.autumn__btn[disabled] :before {
      position: absolute;
      top: -1px;
      left: -1px;
      bottom: -1px;
      right: -1px;
      background: #fff;
      opacity: .35;
      content: "";
      border-radius: inherit;
      z-index: 1;
      -webkit-transition: opacity .2s;
      transition: opacity .2s;
      pointer-events: none;
      display: none; }
.autumn__btn.is-round {
    -wekit-border-radius: 20px;
    border-radius: 20px; }
.autumn__btn.is-long {
    width: 100%;
    height: auto; }
.autumn__btn.no-radius {
    -wekit-border-radius: 0;
    border-radius: 0; }
